___CSS_LOADER_EXPORT___.push([module.id, `/* ベース設定: チェックボックス・ラジオ共通 */
:where(input[type="checkbox"], input[type="radio"]) {
   font-size: inherit;
   line-height: inherit;
   margin: 0;
   block-size: 1rem;
   inline-size: 1rem;
   vertical-align: sub;
}

/* ラジオボタンは非表示（見た目はラベルで表現） */
input[type="radio"] {
   display: none;
   opacity: 0;
}

/* ラベルのベーススタイル */
.radio-group label {
   position: relative;
   display: inline-block;
   font-size: 1rem;
   font-weight: 300;
   opacity: 0.5;
   cursor: pointer;
   transition: opacity 0.3s ease;
}

.radio-group label:hover {
   opacity: 0.7;
}

/* 選択されたときだけラベルにスタイルを適用 */
input[type="radio"]:checked+label {
   opacity: 1;
}

/* チェックボックス */
form input[type="checkbox"] {
   height: 1.45rem;
   border-radius: 2px;
   opacity: 0.8;
}

/* チェックボックスは四角い中身 */
form input[type="checkbox"]::before {
   width: 1rem;
   height: 1rem;
   border-radius: 0;
   opacity: 0;
}

/* チェックされたら内側を表示 */
form input[type="checkbox"]:checked::before,
form input[type="radio"]:checked::before {
   opacity: 1;
}

.contact-form {
   display: grid;
   position: relative;
   /*width: 65%;
   margin: 60vmin auto 0;*/
   width: 50%;
   margin-top: 60vmin;
}

label {
   font-size: 1.2rem;
}

.form-row {
   width: 100%;
   font-weight: 200;
   margin-top: 1vmin;
}

.form-col {
   width: 100%;
}

.col {
   flex: 1;
   width: auto;
}

.col.x-50,
.col.x-100 {
   margin-top: 2rem;
}

.col.x-50:nth-of-type(1) {
   margin-top: 1rem;
}

input:-webkit-autofill,
input:-webkit-autofill:focus {
   transition: background-color 0s 600000s, color 0s 600000s !important;
}

/*.content-wrapper {
   width: 70%;
   margin: 0 auto;
}*/
.get-in-touch {
   margin: 0 auto;
   position: relative;
}

.contact-form .form-field {
   position: relative;
}

.contact-form .input-text {
   font-size: 1.2rem;
   font-weight: 300;
   color: var(--inv);
   display: block;
   width: 100%;
   height: 42pt;
   border-width: 0;
   padding-left: 2rem;
   background: rgba(0, 0, 0, 0.5);
   backdrop-filter: blur(10px);
   box-shadow: inset hsla(0, 0%, 0%, 1) 0px 1px 4px 2px;
   border-radius: 0;
}

.contact-form #message {
   height: 150pt;
   margin: 0 auto;
   padding-top: 1rem;
   box-shadow: inset hsla(0, 0%, 0%, 1) 0px 1px 4px 2px;
}

.contact-form .input-text:focus {
   outline: none;
}

.contact-form .input-text:focus+.label,
.contact-form .input-text:focus+.label span,
.contact-form .input-text.not-empty+.label {
   transform: translateY(-1.75rem);
   opacity: 0.7;
}

.contact-form .label {
   font-weight: 300;
   color: rgba(246, 246, 246, 0.85);
   position: absolute;
   left: 1vmin;
   top: 0.75rem;
   transition: all 0.3s ease-in-out;
}

.contact-form .label span {
   font-size: 1.2rem;
   color: rgba(146, 146, 146, 0.5);
   color: var(--accent);
   letter-spacing: -4px;
   transition: all 0.3s ease;
}

.contact-form .submit-btn {
   font-size: 1.2rem;
   font-weight: 300;
   width: 100%;
   height: 40pt;
   display: inline-block;
   color: var(--inv);
   background: rgba(0, 0, 0, 1);
   backdrop-filter: blur(10px);
   text-transform: uppercase;
   letter-spacing: 0.2rem;
   line-height: 1.5rem;
   padding: 0.5rem 0;
   margin-top: 2rem;
   border: none;
   box-shadow: inset hsla(0, 0%, 0%, 1) 0px 0px 100px 15px;
   cursor: pointer;
   transition: all 0.35s ease;
}

.contact-form .submit-btn:hover {
   color: rgba(160, 160, 160);
   background: var(--inv);
   box-shadow: inset hsla(0, 0%, 0%, 1) 0px 1px 5px 0px;
}

.radio-group,
.align-center h3 {
   font-weight: 200;
   color: var(--inv);
   width: fit-content;
   line-height: 1.5;
   z-index: 1;
}

.radio-group {
   font-size: clamp(1.8rem, 1.467rem + 0.44vw, 2rem);
   display: flex;
   gap: 1.2rem;
   /*margin-top: 10vmin;*/
}

.align-center {
   width: 100%;
   align-items: center;
   margin-top: 3em;
}

.align-center:last-child {
   margin-top: 1.5rem;
}

.align-center p {
