}

/* モーダル表示時 */
.align-center h3 {
   font-size: clamp(2rem, 1.333rem + 0.89vw, 2.4rem);
}

#thanksModal .modal-content p {
   width: fit-content;
   min-width: 50%;
   line-height: 2;
}

#thanksModal .close {
   font-size: 2rem;
   font-weight: bold;
   float: right;
   position: absolute;
   bottom: 12vmin;
   right: 50%;
}

#thanksModal .close:hover,
#thanksModal .close:focus {
   color: var(--accent);
   text-shadow: 0 1px 2px var(--text);
   text-decoration: none;
   cursor: pointer;
}

