n\r\n.contact-form .input-text {\r\n   font-size: 1.2rem;\r\n   font-weight: 300;\r\n   color: var(--inv);\r\n   display: block;\r\n   width: 100%;\r\n   height: 42pt;\r\n   border-width: 0;\r\n   padding-left: 2rem;\r\n   background: rgba(0, 0, 0, 0.5);\r\n   backdrop-filter: blur(10px);\r\n   box-shadow: inset hsla(0, 0%, 0%, 1) 0px 1px 4px 2px;\r\n   border-radius: 0;\r\n}\r\n\r\n.contact-form #message {\r\n   height: 150pt;\r\n   margin: 0 auto;\r\n   padding-top: 1rem;\r\n   box-shadow: inset hsla(0, 0%, 0%, 1) 0px 1px 4px 2px;\r\n}\r\n\r\n.contact-form .input-text:focus {\r\n   outline: none;\r\n}\r\n\r\n.contact-form .input-text:focus+.label,\r\n.contact-form .input-text:focus+.label span,\r\n.contact-form .input-text.not-empty+.label {\r\n   transform: translateY(-1.75rem);\r\n   opacity: 0.7;\r\n}\r\n\r\n.contact-form .label {\r\n   font-weight: 300;\r\n   color: rgba(246, 246, 246, 0.85);\r\n   position: absolute;\r\n   left: 1vmin;\r\n   top: 0.75rem;\r\n   transition: all 0.3s ease-in-out;\r\n}\r\n\r\n.contact-form .label span {\r\n   font-size: 1.2rem;\r\n   color: rgba(146, 146, 146, 0.5);\r\n   color: var(--accent);\r\n   letter-spacing: -4px;\r\n   transition: all 0.3s ease;\r\n}\r\n\r\n.contact-form .submit-btn {\r\n   font-size: 1.2rem;\r\n   font-weight: 300;\r\n   width: 100%;\r\n   height: 40pt;\r\n   display: inline-block;\r\n   color: var(--inv);\r\n   background: rgba(0, 0, 0, 1);\r\n   backdrop-filter: blur(10px);\r\n   text-transform: uppercase;\r\n   letter-spacing: 0.2rem;\r\n   line-height: 1.5rem;\r\n   padding: 0.5rem 0;\r\n   margin-top: 2rem;\r\n   border: none;\r\n   box-shadow: inset hsla(0, 0%, 0%, 1) 0px 0px 100px 15px;\r\n   cursor: pointer;\r\n   transition: all 0.35s ease;\r\n}\r\n\r\n.contact-form .submit-btn:hover {\r\n   color: rgba(160, 160, 160);\r\n   background: var(--inv);\r\n   box-shadow: inset hsla(0, 0%, 0%, 1) 0px 1px 5px 0px;\r\n}\r\n\r\n.radio-group,\r\n.align-center h3 {\r\n   font-weight: 200;\r\n   color: var(--inv);\r\n   width: fit-content;\r\n   line-height: 1.5;\r\n   z-index: 1;\r\n}\r\n\r\n.radio-group {\r\n   font-size: clamp(1.8rem, 1.467rem + 0.44vw, 2rem);\r\n   display: flex;\r\n   gap: 1.2rem;\r\n   /*margin-top: 10vmin;*/\r\n}\r\n\r\n.align-center {\r\n   width: 100%;\r\n   align-items: center;\r\n   margin-top: 3em;\r\n}\r\n\r\n.align-center:last-child {\r\n   margin-top: 1.5rem;\r\n}\r\n\r\n.align-center p {\r\n   /*display: flex;\r\n   gap: 0.25rem;*/\r\n   align-items: center;\r\n   margin: 0;\r\n   line-height: 1.6;\r\n   letter-spacing: 0.015rem;\r\n}\r\n\r\n/*===================== モーダルウィンドウ =====================*/\r\n#thanksModal {\r\n   display: none;\r\n   opacity: 0;\r\n   overflow: hidden;\r\n   position: absolute;\r\n   z-index: 10099;\r\n   background: rgba(119, 119, 119, 0.25);\r\n   transition: opacity 0.4s ease-in-out;\r\n   width: 95vw;\r\n   /*height: 120svh;*/\r\n   min-height: 100vh;\r\n   /*top: 50%;*/\r\n   left: 50%;\r\n   align-content: center;\r\n   transform: translate(-75%, -10%);\r\n   -webkit-backdrop-filter: blur(10px);\r\n   backdrop-filter: blur(10px);\r\n   border: 1px solid var(--sub);\r\n   border-radius: 0;\r\n}\r\n\r\n/* モーダルが表示されるとき */\r\n#thanksModal.show {\r\n   opacity: 1;\r\n}\r\n\r\n/* モーダルコンテンツのアニメーション */\r\n#thanksModal .modal-content {\r\n   position: relative;\r\n   width: 75vw;\r\n   width: -webkit-fill-available;\r\n   height: 70vh;\r\n   margin: 0 auto;\r\n   /*top: 50%;\r\n   left: 50%;\r\n   transform: translate(-50%, -50%);*/\r\n   background: #000;\r\n   box-shadow: inset 1px 1px 4px rgba(0, 0, 0, 1.95), 1px 1px 3px 0 rgba(0, 0, 0, 0.84);\r\n   box-shadow: 1px 1px 3px 0 rgba(0, 0, 0, 0.84);\r\n   display: flex;\r\n   justify-content: center;\r\n   align-items: center;\r\n   flex-direction: column;\r\n   z-index: 11000;\r\n}\r\n\r\n/* モーダル表示時 */\r\n.align-center h3 {\r\n   font-size: clamp(2rem, 1.333rem + 0.89vw, 2.4rem);\r\n}\r\n\r\n#thanksModal .modal-content p {\r\n   width: fit-content;\r\n   min-width: 50%;\r\n   line-height: 2;\r\n}\r\n\r\n#thanksModal .close {\r\n   font-size: 2rem;\r\n   font-weight: bold;\r\n   float: right;\r\n   position: absolute;\r\n   bottom: 12vmin;\r\n   right: 50%;\r\n}\r\n\r\n#thanksModal .close:hover,\r\n#thanksModal .close:focus {\r\n   color: var(--accent);\r\n   text-shadow: 0 1px 2px var(--text);\r\n   text-decoration: none;\r\n   cursor: pointer;\r\n}\r\n\r\n/*===================== モーダルウィンドウ =====================*/\r\n@media screen and (min-width: 601px) and (max-width: 1299px) {\r\n\r\n   .col.x-50,\r\n   .col.x-100 {\r\n      width: 100%;\r\n      padding: 0;\r\n      margin: 1.5rem auto;\r\n   }\r\n\r\n   .contact-form .input-text:focus+.label,\r\n   .contact-form .input-text.not-empty+.label {\r\n      transform: translateY(-2.5rem);\r\n   }\r\n\r\n   .contact-form #message,\r\n   .contact-form .submit-btn,\r\n   .contact-form .label {\r\n      font-size: clamp(1.6rem, 1.256rem + 0.92vw, 2rem);\r\n   }\r\n\r\n   .contact-form .input-text {\r\n      font-size: clamp(1.8rem, 1.628rem + 0.46vw, 2rem);\r\n      height: 40pt;\r\n      padding-left: 1rem;\r\n   }\r\n\r\n   .contact-form .label {\r\n      left: 2.75%;\r\n      top: 13.75%;\r\n      line-height: 2;\r\n   }\r\n\r\n   .contact-form .label-text {\r\n      top: 6.5%;\r\n   }\r\n\r\n   .contact-form .submit-btn {\r\n      height: 28pt;\r\n      padding: 0;\r\n      margin: 0;\r\n      margin-bottom: 0.5rem;\r\n      margin-top: 1rem;\r\n      box-shadow: inset hsla(0, 0%, 0%, 1) 0px 0px 25px 20px;\r\n   }\r\n\r\n   .align-center:last-child {\r\n      margin: 0;\r\n   }\r\n\r\n   .align-center p {\r\n      text-align: left;\r\n   }\r\n\r\n   /*===================== モーダルウィンドウ =====================*/\r\n   #thanksModal {\r\n      width: 100%;\r\n      top: 0%;\r\n      left: 0%;\r\n      transform: translate(0%, 0%);\r\n   }\r\n\r\n   #thanksModal .modal-content {\r\n      width: 90%;\r\n      height: 70vh;\r\n      transform: translate(-50%, -50%);\r\n      background: rgba(245, 252, 246, 0.83);\r\n      -webkit-backdrop-filter: blur(6px);\r\n      backdrop-filter: blur(6.5px);\r\n      border: 1px solid var(--bg);\r\n   }\r\n\r\n   #thanksModal.show {\r\n      width: 100%;\r\n      transform: translate(0%, 0%);\r\n      background: rgba(255, 255, 200, 0.25);\r\n   }\r\n}\r\n\r\n\r\n@media screen and (max-width: 600px) {\r\n   form input[type=\"checkbox\"] {\r\n      height: 1rem;\r\n   }\r\n\r\n   .contact-form {\r\n      width: 100%;\r\n      margin: 15vmin auto 0;\r\n   }\r\n\r\n   .form-row {\r\n      display: block;\r\n   }\r\n\r\n   .col {\r\n      padding: 0;\r\n   }\r\n\r\n   .col.x-50,\r\n   .col.x-50:nth-child(1),\r\n   .col.x-100 {\r\n      padding: 0;\r\n      margin-top: 1.8rem;\r\n   }\r\n\r\n   .contact-form .input-text:focus+.label,\r\n   .contact-form .input-text.not-empty+.label {\r\n      transform: translateY(-1.6rem);\r\n   }\r\n\r\n   /*   .contact-form .submit-btn,\r\n   .align-center h3 {\r\n      font-size: clamp(1.8rem, 1.571rem + 1.14vw, 2rem);\r\n   }*/\r\n   .contact-form .input-text {\r\n      font-size: 1rem;\r\n      height: 40pt;\r\n      padding-left: 1rem;\r\n      background: var(--sub);\r\n   }\r\n\r\n   .contact-form #message {\r\n      line-height: 1.4;\r\n      letter-spacing: 0.01rem;\r\n   }\r\n\r\n.contact-form .label span {\r\n   font-size: 1rem;\r\n}\r\n\r\n   /*.contact-form .label,\r\n   .align-center p,\r\n   .radio-group {\r\n      font-size: clamp(1.4rem, 1.171rem + 1.14vw, 1.6rem);\r\n   }*/\r\n   .contact-form .label {\r\n      font-size: 0.9rem;\r\n      left: 2.75%;\r\n      top: 5.75%;\r\n      line-height: 2;\r\n   }\r\n\r\n   .contact-form .label-text {\r\n      line-height: 1.2;\r\n   }\r\n\r\n   .contact-form .submit-btn {\r\n      font-size: 1rem;\r\n      height: 35pt;\r\n      padding: 0;\r\n      margin: 0;\r\n      margin-top: 10vmin;\r\n   }\r\n\r\n   .contact-form .submit-btn:active {\r\n      text-shadow: 0 2px 4px #000;\r\n      box-shadow: inset hsla(0, 0%, 0%, 1) 0px 0px 95px 0px;\r\n      box-shadow: inset hsl(0deg 0% 19.05%) 0px 0px 55px 0px;\r\n   }\r\n\r\n   .radio-group {\r\n      justify-content: center;\r\n   }\r\n\r\n   .align-center p {\r\n      display: inline-block;\r\n   }\r\n\r\n   /*===================== モーダルウィンドウ =====================*/\r\n   #thanksModal {\r\n      width: 100vw;\r\n      height: 120vh;\r\n      left: 0%;\r\n      padding: 0;\r\n      transform: translate(-50%, -50%);\r\n      /*background: rgba(246, 246, 246, 0.2);*/\r\n   }\r\n\r\n   #thanksModal .modal-content {\r\n      width: 96svw;\r\n      height: 95svh;\r\n      /*top: 50%;*/\r\n      top: -70vmin;\r\n      left: 50%;\r\n      transform: translate(-50%, 70vmin);\r\n   }\r\n\r\n   #thanksModal .modal-content p {\r\n      width: 80%;\r\n   }\r\n\r\n   #thanksModal.show {\r\n      transform: translate(-10%, 20%);\r\n   }\r\n\r\n   #thanksModal .close {\r\n      right: 45%;\r\n      font-size: 1.2rem;\r\n   }\r\n\r\n   /*===================== モーダルウィンドウ =====================*/\r\n}"],"sourceRoot":""}]);
